.cards{
    color: black;
    margin: 0;
    height: 100%;
    
}
.cards:hover{
    transition: 0.2s;
    transform: scale(1.01);
    box-shadow: 0px 0px 18px -1px rgba(0,0,0,0.69);
}
a, a:active, a:link, a:visited{
    text-decoration: none;
    text-align: center;
    color: black;
}
.bodegas-cards{
    image-resolution: 300dpi;
    object-fit: contain!important;
}
